@import "../../styles/responsive";

.container {
  padding: 8% 15%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  .subText {
    color: #9090a0;
    line-height: 2rem;
  }
  .mr5 {
    margin-right: 1rem;
  }
  .select {
    width: 30%;
    margin: 5%;
  }

  .planCards {
    display: flex;
    align-items: flex-start;
    align-content: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    text-align: center;
    justify-content: space-around;
  }

  @include mobile {
    padding-top: 30%;

    h2 {
      font-size: 1.1rem;
    }

    .mr5 {
      margin-right: 0;
      margin-bottom: 1rem;
    }

    .select {
      width: 100%;
      flex-wrap: wrap;
    }
  }

  @include mobileLandscape { 
    padding-top: 15%;
    .select { 
      width: 80%;
    }
  }

}
