@import "../../styles/variables";
@import "../../styles/colors";

.container {
  display: flex;
  flex-direction: column;
  width: 100%;

  .label {
    color: #9090A0;
    font-size: 0.9rem;
    margin: 0.5rem 0;
  }

  .select {
    color: $secondary;
    border: 1px solid $secondary;
    font-family: $font-poppins;
    font-size: 1rem;
    border-radius: 6px;
    cursor: pointer;
    outline: none;
    min-width: 100px;
    padding: 0.5rem;
  }
}

